:root {
  --accent-color: #ff6b08;
  --main-bg-color: #e5e5e5;
  --second-bg-color: #f7f7f7;
  --main-text-color: #fff;
  --second-text-color: #545454;
  --third-text-color: #000;
  --fourth-text-color: #8c8c8c;
}

.darkmode {
  --accent-color: #ff6b08;
  --main-bg-color: #595959;
  --second-bg-color: #8c8c8c;
  --main-text-color: #fff;
  --second-text-color: #f2f2f2;
  --third-text-color: #e5e5e5;
  --fourth-text-color: #ddd;
}

body {
  width: 100%;
  background-color: var(--main-bg-color);
  color: var(--main-text-color);
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
