.slide {
  cursor: pointer;
  background-color: #a88e71;
  transition: all .9s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@media screen and (min-width: 1024px) {
  .slide {
    top: 10px;
    left: 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .slide {
    top: 10px;
    left: 20px;
  }
}

@media screen and (min-width: 1px) and (max-width: 767px) {
  .slide {
    top: 30px;
    left: 15px;
  }
}

.slide:before {
  content: "";
  height: 20px;
  width: 20px;
  background-color: #29343d;
  transition: all .9s;
  position: absolute;
  bottom: 2.5px;
  left: 5px;
}

input:checked + .slide {
  background-color: #ff6b08;
}

input:checked + .slide:before {
  transform: translateX(30px);
}

.slide.round {
  border-radius: 80px;
}

.slide.round:before {
  border-radius: 50%;
}

