@media screen and (min-width: 320px) {
  body {
    overflow-x: scroll;
  }

  body.modal-open {
    overflow: hidden;
  }

  .is-hidden.backdrop {
    visibility: 0;
    opacity: 0;
    pointer-events: none;
  }

  .is-hidden.backdrop .modal {
    opacity: 0;
  }

  .backdrop {
    width: 100%;
    height: 100%;
    color: var(--third-text-color);
    background: rgba(0, 0, 0, .2);
    font-family: Roboto;
    transition-property: opacity;
    transition-duration: .25s;
    position: fixed;
    top: 0;
    left: 0;
  }

  .modal__box {
    max-height: 85vh;
    width: 280px;
    background-color: var(--main-bg-color);
    border-radius: 5px;
    padding-top: 48px;
    padding-bottom: 40px;
    display: flex;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 15px 5px rgba(255, 107, 8, .75);
  }

  .modal {
    width: 280px;
    background-color: var(--main-bg-color);
    scrollbar-width: none;
    padding-left: 20px;
    padding-right: 20px;
    transition-property: scale, transform, opacity;
    transition-duration: .3s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    overflow-y: scroll;
  }

  .modal::-webkit-scrollbar {
    width: 0;
    height: 0;
  }

  .modal__close-button {
    width: 30px;
    height: 30px;
    cursor: pointer;
    fill: var(--third-text-color);
    z-index: 16;
    background-color: rgba(0, 0, 0, 0);
    justify-content: center;
    align-items: center;
    transition-property: color;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    top: 8px;
    right: 8px;
  }

  .modal__close-button:hover, .modal__close-button:focus {
    color: #ff6b08;
  }

  .modal__icon-close {
    width: 1em;
    height: 1em;
    stroke-width: 0;
    stroke: currentColor;
    fill: currentColor;
    width: 30px;
    height: 30px;
    transition-property: color;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    display: inline-block;
  }

  .modal__icon-close:hover, .modal__icon-close:focus {
